define('fifa-trade/routes/index', ['exports'], function (exports) {
	exports['default'] = Ember.Route.extend({
		model: function model() {
			var restUrl = this.controllerFor('index').get('restUrl');
			$.ajax({
				'url': restUrl + 'getMenu',
				'type': 'POST',
				'dataType': 'json'
			});
		}
	});
});